//SuecaSlab
@font-face {
    font-family: 'SuecaSlab';
    src: url('../fonts/SuecaSlab/suecaslab-medium.eot');
    src: url('../fonts/SuecaSlab/suecaslab-medium.eot?#iefix') format('embedded-opentype'),
        url('../fonts/SuecaSlab/suecaslab-medium.woff2') format('woff2'),
        url('../fonts/SuecaSlab/suecaslab-medium.woff') format('woff'),
        url('../fonts/SuecaSlab/suecaslab-medium.ttf') format('truetype'),
        url('../fonts/SuecaSlab/suecaslab-medium.svg#suecaslab-medium') format('svg');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'SuecaSlab';
    src: url('../fonts/SuecaSlab/suecaslab-bold.eot');
    src: url('../fonts/SuecaSlab/suecaslab-bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/SuecaSlab/suecaslab-bold.woff2') format('woff2'),
        url('../fonts/SuecaSlab/suecaslab-bold.woff') format('woff'),
        url('../fonts/SuecaSlab/suecaslab-bold.ttf') format('truetype'),
        url('../fonts/SuecaSlab/suecaslab-bold.svg#suecaslab-bold') format('svg');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}